<template>
  <el-input :style='{"min-width": 8*placeholder.length*2 + "px"}' :placeholder='placeholder' v-bind='$attrs' @change='onChange'></el-input>
</template>

<script>
  import {ElInput} from 'element-ui'
  export default {
    name: 'BInput',
    extends: ElInput,
    props: {
      placeholder: {
        type: String,
        default: ''
      }
    },
    created () {
      // console.log(this)
    },
    methods: {
      onChange (val) {
        this.$emit('change', val)
      }
    }
  }
</script>



